<template>
  <div class="banner" v-if="quickEnter">
    <swiper :options="swiperOption">
      <swiper-slide  v-for="(item, index) in quickEnter" :key="index">
        <a style="display:block" :href="item.router" :title="item.title"><img class="banner-img" :src="item.cover" alt></a>
      </swiper-slide>
      <div class="swiper-button-prev" slot="button-prev">
        <div class="my-pre"></div>
      </div>
      <div class="swiper-button-next" slot="button-next">
        <div class="my-next"></div>
      </div>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  data() {
    return {
      swiperOption: {
        spaceBetween: 100,
        centeredSlides: true,
        autoplay: {
          delay: 2000,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          bulletClass: "my-pagination-bullet ",
          bulletActiveClass: "my-pagination-bullet-active"
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
        // loop: true
      }
    };
  },
  name: "banner-play",
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    ...mapState("news", ["quickEnter"])
  },
  methods: {
    ...mapActions("news", ["get_news_banner"])
  },
  mounted() {
    this.get_news_banner();
  }
};
</script>
