<template>
  <div class="sort">
    <!-- 列表排序方式 -->
    <ul>
      <li
        v-for="(val,index) in sort"
        :key="index"
        :class="{'li-actived':val.actived}"
        @click="activeSort(index)"
      >
        {{val.name}}
        <img :src="val.actived ? i_down_arrow_actived : i_down_arrow">
      </li>
      <div class="price">
        <span>￥</span>
        <input type="number" v-model="min_price" id="min-price">
        <span class="split">-</span>
        <input type="number" v-model="max_price" id="max-price">
      </div>
    </ul>
  </div>
</template>

<script>
import { i_sort } from "@/assets/img.js";
import { mapState } from "vuex";
export default {
  data() {
    return {
      i_down_arrow: i_sort.downArrow,
      i_down_arrow_actived: i_sort.downArrowActived,
      min_price: 0,
      max_price: ""
    };
  },
  computed: {
    ...mapState("secondHand", ["sort"])
  },
  methods: {
    activeSort(index) {
        this.sort.forEach((d,i)=>{
            d.actived = index === i
        })
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./css/sort.less");
</style>

