<template>
  <div class="person-header">
    <div class="content">
      <div class="head-img">
        <img :src="headImg" alt>
      </div>
      <div class="name">{{name}}</div>
    </div>
  </div>
</template>

<script>
import { i_person_header } from "@/assets/img.js";
import { imgHeader } from "@/utils/js/config.js";

export default {
  data() {
    return {
      i_user: this.$store.state.account.user.avatar,
      name:this.$store.state.account.user.name
    };
  },
  computed: {
    headImg() {
      return this.i_user
        ? imgHeader + this.i_user
        : imgHeader + "lij_circle_logo.png";
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./css/personHeader.less");
</style>
