import {
    GET_SECONDHAND_LIST,
    GET_SECONDHAND_DETAIL
} from '../mutaion-type'
import {
    api_post_getSecondList,
    api_get_getSecondDetail
} from '../../api/secondHand'
export const secondHand = {
    namespaced: true,
    state: {
        types:[{
            type:'all',
            name:'全部',
            actived:true
        },{
            type:'electronic',
            name:'电子产品',
            actived:false
        },{
            type:'makeup',
            name:'美妆',
            actived:false
        },{
            type:'costume',
            name:'服饰',
            actived:false
        },{
            type:'book',
            name:'图书资料',
            actived:false
        },{
            type:'daily',
            name:'生活用品',
            actived:false
        },{
            type:'outdoor',
            name:'户外运动',
            actived:false
        },{
            type:'music',
            name:'玩具乐器',
            actived:false
        },{
            type:'tenement',
            name:'租房',
            actived:false
        },{
            type:'else',
            name:'其他',
            actived:false
        }],
        sort:[{
            type:'view',
            name:'浏览量',
            actived:true   
        },{
            type:'time',
            name:'上架时间',
            actived:false  
        },{
            type:'price',
            name:'价格',
            actived:false   
        }],
        secondList:'',
        secondHandDetail:''
    },
    mutations: {
        [GET_SECONDHAND_LIST](state, params) {
            console.log('get secondHand\'s list',params);
            return state.secondList = params
        },
        [GET_SECONDHAND_DETAIL](state, params) {
            console.log('get secondHand\'s detail',params);
            return state.secondHandDetail = params
        },
    },
    actions: {
        async post_getSecondList({
            commit,
            state
        },options) {
            const r = await api_post_getSecondList(options)
            commit(GET_SECONDHAND_LIST, r.data)
        },
        async get_getSecondDetail({
            commit,
            state
        },itemId) {
            const r = await api_get_getSecondDetail(itemId)
            commit(GET_SECONDHAND_DETAIL, r)
        }
    }
}