<template>
    <div class="type">
        <!-- 用于选择二手物品种类 -->
        <div class="part-1">
            <div class="left">请选择种类<span>（可多选）</span>：</div>
            <div class="right">
                <input type="text" name="" id="search-items" placeholder="商品名称/商家">
                <label for="search-items"><img :src="i_search" alt=""></label>
            </div>
        </div>
        <!-- 所有带选项 -->
        <div class="part-2">
            <ul class="clearfix">
                <li :class="{'li-actived':val.actived}" @click="val.actived=!val.actived" v-for="(val,index) in types" :key="index" >{{val.name}}</li>
            </ul>
            <div class="clear-all"><img @click="clearAll" :src="i_clear_all" alt="" title="清除选项"></div>
        </div>
    </div>
</template>

<script>
import { i_type } from "@/assets/img.js";
import { mapState } from 'vuex';
export default {
    data() {
        return {
            i_search:i_type.search,
            i_clear_all:i_type.clearAll
        }
    },
    computed: {
        ...mapState('secondHand',['types'])
    },
    methods: {
        clearAll(){
            this.types.forEach(d=>d.actived = false)
        }
    },
}
</script>

<style lang="less" scoped>
@import url('./css/type.less');
